*{
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 960px;
  margin: 0 auto !important;
  padding: 20px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  background: #f0f0f0;
  color: #494949;
  padding: 20px;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.19);
  border-radius: 10px;
  text-align: center;
  margin-top: -30px;
}
header h3{
  text-align: left;
  margin-top: 10px;
  padding-left: 20px;
}
.signOut{
  width: 100px;
  float: right;
  margin-top: 15px;
  background:none;
  border: none;
  color: #2a7ea3;
}
.signOut:hover{
  text-decoration: underline;
}
header p {
  font-size: 11px;
  color: #adadad;
}
main{
  padding: 20px;
}

.form-group *{
  width: 100%;
  border-radius: 10px;
  border: none;
  
}
  #noteform-title, #noteform-note, .form-group input{
    border: 1px solid rgba(0, 0, 0, 0.09);
    line-height: 50px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.09);
    padding: 0 20px;
  }
  button{
    width: 100%;
  }


.notes-wrapper{
  text-align: center;
  margin-bottom: 150px !important;
}
  .notes-wrapper > h3, .noteform > h3{
    padding: 10px;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.07);
    border-radius: 10px;
    margin-top: 20px;
    color: #585858;
    margin-bottom: 20px;
    text-align: center;
  }


.note{
  width: 45%;
}
  .col:not(:first-child), .col:not(:last-child){
    padding-right: 10px;
    padding-left: 10px;
  }
  .content{
    background: #fffff2;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.19);
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 20px;
  }
  .note h3{
    font-size: 18px;
  }
  .note p{
    font-size: 12px;
    background: #fff;
    border: 1px solid rgba(0, 0, 0, 0.09);
    border-radius: 10px;
    padding: 10px;
    text-align: left;
  }
  .remove{
    float: right;
    margin-top: -30px;
    background-color: #bd3939;
    color: rgb(255, 219, 219);
    border-radius: 1000px;
    text-align: center;
    line-height: 17px;
    vertical-align: top;
    padding: 5px;
    width: 20px;
    height: 20px;
    padding: 0 !important;
    cursor: pointer;
  }


/* login */
.registerContainer h1{
  text-align: center;
  margin-bottom: 30px;
  background: #f0f0f0;
  color: #494949;
  padding: 20px;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.19);
  border-radius: 10px;
  text-align: center;
  margin-top: -30px;
  color: #494949;
}